<template>
  <view v-if="goods_info.goods_name">
    <!-- 轮播图区域 -->
    <swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000" :circular="true">
      <swiper-item v-for="(item, i) in goods_info.pics" :key="i">
        <image :src="item.pics_big" @click="preview(i)"></image>
      </swiper-item>
    </swiper>

    <!-- 商品信息区域 -->
    <view class="goods-info-box">
      <!-- 商品价格 -->
      <view class="price">￥{{goods_info.goods_price}}</view>
      <!-- 信息主体区域 -->
      <view class="goods-info-body">
        <!-- 商品名称 -->
        <view class="goods-name">{{goods_info.goods_name}}</view>
        <!-- 收藏 -->
        <!--  <view class="favi">
         <uni-icons type="star" size="18" color="gray"></uni-icons>
         <text>收藏</text>
       </view> -->
      </view>
      <!-- 运费 -->
      <view class="yf">快递：免运费 </view>

      <!-- 商品详情信息 -->
      <rich-text :nodes="goods_info.goods_describe"></rich-text>
      <view v-for="(item,index) in goods_info.pics" :key="index" class="dsc_pic">
        <image :src="item.pics_big"></image>
      </view>
      <!-- 商品导航组件 -->
      <view class="goods_nav">
        <!-- fill 控制右侧按钮的样式 -->
        <!-- options 左侧按钮的配置项 -->
        <!-- buttonGroup 右侧按钮的配置项 -->
        <!-- click 左侧按钮的点击事件处理函数 -->
        <!-- buttonClick 右侧按钮的点击事件处理函数 -->
        <uni-goods-nav :fill="true" :options="options" :buttonGroup="buttonGroup" @click="onClick"
          @buttonClick="buttonClick" />

      </view>
      <uni-popup ref="popup" type="bottom" background-color="#fff">
        <my-pop @close='close' :goods_info="goods_info" :info='info'></my-pop>
      </uni-popup>
    </view>
  </view>
</template>

<script>
  const goodsdetail = require('../../data/goodsdetail.json')
  import {
    mapState,
    mapMutations,
    mapGetters
  } from 'vuex'
  export default {
    computed: {
      // 调用 mapState 方法，把 m_cart 模块中的 cart 数组映射到当前页面中，作为计算属性来使用
      // ...mapState('模块的名称', ['要映射的数据名称1', '要映射的数据名称2'])
      ...mapState('m_cart', ['cart']),
      ...mapGetters('m_cart', ['total'])
    },
    data() {
      return {
        // 商品详情对象
        goods_info: goodsdetail.message,
        goods_id: Number,
        // 左侧按钮组的配置对象
        options: [{
          icon: 'shop',
          text: '首页'
        }, {
          icon: 'cart',
          text: '购物车',
          info: 3
        }],
        // 右侧按钮组的配置对象
        buttonGroup: [{
            text: '加入购物车',
            backgroundColor: '#ff0000',
            color: '#fff'
          },
          {
            text: '立即购买',
            backgroundColor: '#ffa200',
            color: '#fff'
          }
        ],
      //是加入购物车还是立即购买
       info:''
      } 
    },
    methods: {
      // 把 m_cart 模块中的 addToCart 方法映射到当前页面使用
      ...mapMutations('m_cart', ['addToCart'], ),
      // 定义请求商品详情数据的方法
      // async getGoodsDetail(goods_id) {
      //   const {
      //     data: res
      //   } = await this.$myHttp({
      //     url: 'api/public/v1/goods/detail',
      //     data: {
      //       goods_id: goods_id
      //     }
      //   });
      //   console.log(res.message,"log");
      //   // 使用字符串的 replace() 方法，为 img 标签添加行内的 style 样式，从而解决图片底部空白间隙的问题
      //   // 使用字符串的 replace() 方法，将 webp 的后缀名替换为 jpg 的后缀名
      //   res.message.goods_introduce = res.message.goods_introduce.replace(/<img /g, '<img style="display:block;" ')
      //     .replace(/webp/g, 'jpg')
      //   // 为 data 中的数据赋值
      //   this.goods_info = res.message
      // },
      // 实现轮播图的预览效果
      preview(i) {
        // 调用 uni.previewImage() 方法预览图片
        uni.previewImage({
          // 预览时，默认显示图片的索引
          current: i,
          // 所有图片 url 地址的数组
          urls: this.goods_info.pics.map(x => x.pics_big)
        })
      },
      // 左侧按钮的点击事件处理函数
      onClick(e) {
        if (e.content.text === '购物车') {
          // 切换到购物车页面
          uni.switchTab({
            url: '/pages/cart/cart'
          })
        }
      },
      buttonClick(e) {
        // 1. 判断是否点击了 加入购物车 按钮
        if (e.content.text === '加入购物车') {
          this.info='加入购物车'
          this.open()
        }
        if (e.content.text === '立即购买') {
          this.info='立即购买'
          this.open()
        }
      },
      open() {
        // 通过组件定义的ref调用uni-popup方法 ,如果传入参数 ，type 属性将失效 ，仅支持 ['top','left','bottom','right','center']
        this.$refs.popup.open('bottom')
      },
      close() {
        console.log("关闭弹窗");
        this.$refs.popup.close()
      }
    },
    watch: {
      // 定义 total 侦听器，指向一个配置对象
      total: {
        // handler 属性用来定义侦听器的 function 处理函数
        handler(newVal) {
          const findResult = this.options.find(x => x.text === '购物车')
          if (findResult) {
            findResult.info = newVal
          }
        },
        // immediate 属性用来声明此侦听器，是否在页面初次加载完毕后立即调用
        immediate: true
      }
    },
    onLoad(options) {
      // 获取商品 Id
      this.goods_id = +options.goods_id
      // 调用请求商品详情数据的方法
      // this.getGoodsDetail(this.goods_id)
    }
  }
</script>

<style lang="scss" scoped>
  swiper {
    height: 550rpx;

    image {
      width: 100%;
      height: 100%;
    }
  }

  // 商品信息区域的样式
  .goods-info-box {
    padding: 10px;
    padding-right: 0;

    .price {
      color: #c00000;
      font-size: 18px;
      margin: 10px 0;
    }

    .goods-info-body {
      display: flex;
      justify-content: space-between;

      .goods-name {
        font-size: 13px;
        padding-right: 10px;
      }

      // // 收藏区域
      // .favi {
      //   width: 120px;
      //   font-size: 12px;
      //   display: flex;
      //   flex-direction: column;
      //   justify-content: center;
      //   align-items: center;
      //   border-left: 1px solid #efefef;
      //   color: gray;
      // }
    }

    // 运费
    .yf {
      margin: 10px 0;
      font-size: 12px;
      color: gray;
    }
  }

  .dsc_pic {
    margin-top: 10px;
    height: 260px;

    image {
      height: 100%;
      width: 100%;
    }
  }

  // 美化商品导航组件，使之固定在页面最底部
  .goods-detail-container {
    // 给页面外层的容器，添加 50px 的内padding，
    // 防止页面内容被底部的商品导航组件遮盖
    padding-bottom: 50px;
  }

  .goods_nav {
    // 为商品导航组件添加固定定位
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
  }
</style>
